<template>
  <div id="articleUpload">
    <div class="function">
      <el-button
        type="danger"
        @click="postArtilce"
      ></el-button>
      <el-button></el-button>
    </div>
    <div class="title">
      <el-input
        id="title"
        type="text"
        placeholder=""
        v-model="title"
      >
        <el-button
          slot="prepend"
          icon="el-icon-upload"
          @click="clickUploadMarkdown"
        ></el-button>
        <el-checkbox
          v-model="pinned"
          slot="append"
        ></el-checkbox>
      </el-input>
      <input
        type="file"
        id="uploadMarkdown"
        @change="uploadMarkdown"
      >

    </div>
    <div class="info">
      <el-date-picker
        v-model="date"
        type="date"
        class="date"
        value-format="yyyy-MM-dd"
        placeholder=""
      >
      </el-date-picker>
      <el-autocomplete
        class="inline-input tag"
        :fetch-suggestions="querySearch"
        prefix-icon="el-icon-star-off"
        v-model="tag"
        placeholder=""
      >
      </el-autocomplete>
      <!-- <el-checkbox v-model="pinned"></el-checkbox> -->
    </div>
    <div class="content">
      <el-input
        type="textarea"
        :autosize="{ minRows: 10, maxRows: 10}"
        placeholder=""
        v-model="content"
      >
      </el-input>
    </div>
    <div class="img">
      <el-button
        type="primary"
        @click="clickUploadImg"
      ></el-button>
      <el-button
        type="danger"
        @click="clearUploadImg"
        v-if="imgList[0]"
      ></el-button>
      <input
        id="uploadImg"
        type="file"
        accept="image/*"
        @change="uploadImg($event)"
        multiple
      >
      <div
        class="imgList"
        v-if="imgList[0]"
      >
        <div
          class="img-wrap"
          v-for="img in imgList"
          :key="img.name"
        >
          <img :src="img.file">
          <i
            class="el-icon-delete deleteImg"
            @click="deleteImg(img.name)"
          ></i>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios"
import $ from "jquery"
export default {
  data () {
    return {
      title: "",
      pinned: false,
      date: this.getDate(),
      tag: "",
      taglist: "",
      content: "",
      imgList: [],
      imgNameList: [],
    }
  },
  methods: {
    getDate () {
      var d = new Date();
      return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
    },
    getTagList () {
      axios
        .get('/api/getTagList')
        .then(res => {
          let taglist = res.data.tags
          this.taglist = taglist.map(e => {
            if (e.tagName == '') {
              return {
                value: ''
              }
            } else {
              return {
                value: e.tagName
              }
            }
          })
        })
    },
    querySearch (queryString, cb) {
      var taglist = this.taglist;
      var results = queryString ? taglist.filter(this.createFilter(queryString)) : taglist;
      //  callback 
      cb(results);
    },
    createFilter (queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    uploadMarkdown () {
      var objFile = document.getElementById("uploadMarkdown");
      if (objFile.value == "") {
        alert("")
        return
      }

      const files = objFile.files
      const filename = objFile.files[0].name
      //   console.log(filename)
      var patt = /\.md$/
      if (!patt.test(filename)) {
        // console.log(filename + 'is not markdown')
        this.$notify.error({
          title: '',
          message: 'Markdown'
        });
        return
      }
      // FileReader
      const reader = new FileReader()
      //  
      reader.readAsText(files[0], "UTF-8")
      // 
      this.title = files[0].name
      let that = this
      reader.onload = function (e) {
        // 
        const fileString = e.target.result
        // 
        that.content = fileString
      }
    },
    async uploadImg (event) {
      const files = event.target.files
      const uploadList = []
      //   console.log(files)

      const readFileAsync = file => new Promise(resolve => {
        const reader = new FileReader()
        reader.onload = evt => resolve(evt.target.result)
        reader.readAsDataURL(file)
      })

      for (let i = 0; i < files.length; i++) {
        let img = {
          name: files[i].name,
          file: await readFileAsync(files[i])
        }
        if (this.imgNameList.indexOf(img.name) == -1) {
          this.imgList.push(img)
          //nameimgNameListimgListbase64
          this.imgNameList.push(img.name)
        }
        else {
          this.$notify({
            title: '',
            message: '',
            type: 'warning'
          });
        }
      }

      event.target.value = null
      //    this.imgList = this.imgList.concat(uploadList)
      //   console.log(uploadList)
    },
    clearUploadImg () {
      this.imgList = []
      this.imgNameList = []
    },
    postArtilce () {
      if (this.title == '') {
        this.$notify.error({
          title: '',
          message: ''
        });
        return
      }

      if (this.content == '') {
        this.$notify.error({
          title: '',
          message: ''
        });
        return
      }
      if (this.tag == '') {
        this.$notify.error({
          title: '',
          message: '<>'
        });
        return
      }
      if (this.tag == '') {
        this.tag = ""
      }
      var that = this
      console.log(this.imgList)
      axios.post('/admin/postArticle', {
        params: {
          title: this.title,
          content: this.content,
          tag: this.tag,
          pinned: this.pinned,
          date: this.date,
          img: this.imgList,
        }
      }).then(res => {
        that.$alert('', '')
      })
    },
    clickUploadMarkdown () {
      document.getElementById("uploadMarkdown").click();
    },
    clickUploadImg () {
      document.getElementById("uploadImg").click();
    },
    deleteImg (name) {
      //   console.log(this.imgList)
      console.log(name)
      //   console.log(this.imgNameList.remove(name))
      console.log(this.imgNameList.indexOf(name))
      console.log(this.imgNameList.splice(this.imgNameList.indexOf(name), 1))
    }
  },
  mounted () {
    this.getTagList()

  }
}
</script>

